import React, {Component} from 'react';
import {Table} from 'semantic-ui-react'
import color from '../colors';

const timeoutLength = 2500

class Item extends Component {

    wasModifiedToday = (item,e) => {
        // console.log(item.timestamp);
        return ((new Date()).toDateString() === (new Date(item.timestamp)).toDateString());
    }

    stopPropagation = (e) => {
        e.stopPropagation();
    }

    render() {
        const props = this.props;
        const selected = (props.item.name === props.selectedItem.name);
        return (
            <Table.Row 
                // className={selected?'itemRowSelected':null} 
                style={{backgroundColor: selected?'#212223':color(props.item.tags)}} 
                onClick={props.rowClickEvent} 
            >
                <Table.Cell textAlign="center" width={4} 
                    style={{padding:0, lineHeight:1}}
                >
                    {/* {(props.item.qty.length>0)?props.item.qty:"--"} <br/> {props.item.unit} */}
                    {props.item.qty} <br/> {props.item.unit}
                </Table.Cell>
                <Table.Cell width={12}
                    style={{lineHeight:1, whiteSpace:'preWrap'}}
                > 
                    {props.item.name} <strong >{props.item.marked ? " *" : ""}</strong>
                    <br/>
                    <i>{props.item.notes}</i>
                </Table.Cell>
            </Table.Row>
        )
    }
}

export default Item;




