import React, { Component } from 'react';
import {
    Button,
    List,
    Segment,
    TransitionablePortal
} from 'semantic-ui-react';
// import HoldButton from './HoldButton'
import PopupNotes from './PopupNotes';
import Sidebar from './Sidebar';

class Keypad extends Component {
    state = {keepKeypadOpen :false};

    keepKeypadOpen = () => {
        this.setState({keepKeypadOpen:!this.state.keepKeypadOpen})
      }
    
    stopPropagation = (e) => {
      // if (!this.state.keepKeypadOpen)
        e.stopPropagation();
    }

    handleTouchStart = (e) => {
      e.target.className="ui button grey";
    }

    handleTouchEnd = (e) => {
      e.target.className="ui button black";
    }
  render() {
    
    const key = (label) => <Button className="ui black button" onClick={this.props.numberClicked} onTouchStart={this.handleTouchStart} onTouchEnd={this.handleTouchEnd} style={{textAlign:'center', paddingLeft:0, paddingRight:0, margin:0, borderRadius:"0em"}}>{label}</Button>

    return (
      <div>
        <TransitionablePortal 
            open={this.props.open} 
            transition={{animation:'horizontal flip', duration:500}} 
            // closeOnDocumentClick={!this.state.keepKeypadOpen} 
            closeOnDocumentClick={false}
//            onClose={this.props.onClose}
        >
        {/* <div style={{ backgroundColor: 'transparent', left: '0%', padding:0, position: 'fixed', top: '54px', zIndex: 5001, width:"100%", height:"100%", borderRadius:0}}
          onClick={this.props.onClose}
          // onClick={this.state.keepKeypadOpen? null: this.props.onClose}
          > */}
          <Segment textAlign="center" basic inverted className="ui black"
            style={{
              borderStyle:'none',
              right: '0%',
              padding:'0px 0px',
              position: 'fixed',
              top: '62px',
              zIndex: 5001,
              width:"50%",
              height:"auto",
              borderRadius:0
            }}
            onClick={this.stopPropagation}
          >
            {/* <Container style={{height:40}}>
              <Header inverted size="huge" onClick={this.props.deleteClicked} >
                {this.props.item.qty}
                <Header sub size="huge">
                {this.props.item.unit}
                </Header>  
              </Header>
              </Container>
            <p/> */}
            {/* <Segment textAlign="center" inverted basic className="ui black" style={{borderRadius:0, borderStyle:'none', padding:0,margin:0}}> */}
            <PopupNotes
                item={this.props.item} 
                trigger={
                  <Button disabled={this.props.item.name.length === 0} icon="edit" className="black" content="Add note" style={{margin:5, marginTop:0}}/>
                }
                {...this.props}
              />
              <Button.Group size="big"  widths="3" >
                {key("¼")}
                {key("½")}
                {key("¾")}
              </Button.Group>
              <Button.Group size="big"  widths="3" >
                {key(1)}{key(2)}{key(3)}
              </Button.Group>
              <Button.Group size="big"  widths="3" >
                {key(4)}{key(5)}{key(6)}
              </Button.Group>
              <Button.Group size="big"  widths="3" >
                {key(7)}{key(8)}{key(9)}
              </Button.Group>
              <Button.Group size="big"  widths="3" >
                <Button className="ui black button" icon="asterisk" onClick={this.props.asteriskClicked} style={{textAlign:'center', paddingLeft:0, paddingRight:0, margin:0, borderRadius:"0em"}}/>
                {key(0)}
                <Button className="ui black button" icon="delete" onClick={this.props.deleteClicked} style={{textAlign:'center', paddingLeft:0, paddingRight:0, margin:0, borderRadius:"0em"}}/>
              </Button.Group>
              <List size="small" selection celled inverted horizontal style={{margin:0, marginTop:5, marginBottom:15 }}>
              <List.Item style={{padding:10, paddingTop:12, paddingBottom:12}} onClick={this.props.unitClicked}>bags</List.Item>
              <List.Item style={{padding:10, paddingTop:12, paddingBottom:12}} onClick={this.props.unitClicked}>boxes</List.Item>
              <List.Item style={{padding:10, paddingTop:12, paddingBottom:12}} onClick={this.props.unitClicked}>trays</List.Item>
              <List.Item style={{padding:10, paddingTop:12, paddingBottom:12}} onClick={this.props.unitClicked}>tubs</List.Item>
              <List.Item style={{padding:10, paddingTop:12, paddingBottom:12}} onClick={this.props.unitClicked}>bin</List.Item>
              <List.Item style={{padding:10, paddingTop:12, paddingBottom:12}} onClick={this.props.unitClicked}>shelf</List.Item>
              <List.Item style={{padding:10, paddingTop:12, paddingBottom:12}} onClick={this.props.unitClicked}>pcs</List.Item>              
            </List>
            {/* </Segment> */}
            <Button className="black"
              onClick={this.props.showSimilar.bind(this, this.props.item.tags)}
            >
              Similar Items
            </Button>
            <Button icon="check" size="huge" inverted onClick={this.props.doneClicked} style={{borderRadius:0, margin:20, opacity:0.7}}/>
          </Segment>
          {/* </div> */}
        </TransitionablePortal>
      </div>
    );
  }
}

export default Keypad;



