import React, { Component } from 'react';
import {TransitionablePortal, Segment, Label, Button, Header, Grid} from 'semantic-ui-react';
import color from '../colors';
// import CategoryItems from './CategoryItems'

class CategoryPortal extends Component {

    render() {

        const renderCategory = (category, index) => {
            return (
                <Grid.Column key={index}
                        onClick={this.props.categorySelected.bind(this,category)}
                        style={{
                            padding:0,
                            paddingTop:5
                            // backgroundColor:"gray"
                        }}
                    >
                    <Label
                        style={{
                            backgroundColor:'transparent', 
                            color:color(category)                    
                        }}
                    >
                        {category}
                    </Label>
                </Grid.Column>
            )
        }   

        const fruitItems = this.props.items.filter(item => (item.type === "Fruit"));
        const fruitCategories = [...new Set(fruitItems.map(item => item.tags))].sort();
        const renderedFruitCategories = fruitCategories.map((category,index) => {
            return renderCategory(category, index);
        });
        const vegItems = this.props.items.filter(item => (item.type === "Veg"));
        const vegCategories = [...new Set(vegItems.map(item => item.tags))].sort();
        const renderedVegCategories = vegCategories.map((category,index) => {
            return renderCategory(category, index);
        });

        return (
            <div>
                <TransitionablePortal 
                    open={this.props.open} 
                    transition={{animation:'horizontal flip', duration:500}}  
                    closeOnDocumentClick={false}
                >
                    <Segment inverted textAlign="center"
                        style={{
                            borderRadius:"0em",
                            position: 'fixed',
                            top: '60px',
                            right: '0%',
                            width:"50%",
                            height:"auto",
                            margin:0,
                            paddingTop:10,
                            paddingBottom:0,
                            zIndex: 5004
                        }}
                    >
                        <Header dividing disabled content="ALL FRUIT" inverted size="tiny"
                            onClick={this.props.fruitSelected.bind(this, 'All Fruit',"*")}
                            style={{marginBottom:16}}
                        />
                        <Grid columns={2} verticalAlign="middle">                        
                            {renderedFruitCategories}
                        </Grid>
                        <Header dividing disabled content="ALL VEG" inverted size="tiny"
                            onClick={this.props.vegSelected.bind(this, 'All Veg',"*")}
                            style={{marginBottom:16}}
                        />
                        <Grid columns={2} verticalAlign="middle">                        
                            {renderedVegCategories}
                        </Grid>
                    </Segment>
                </TransitionablePortal>
            </div>
        );
    }
}

export default CategoryPortal;



