import React, { Component } from 'react';
import './App.css';
import {
  Button,
  Input,
  Label,
  Table, 
  Header,
  Menu,
} from 'semantic-ui-react';
//import TransitionGroup from 'semantic-ui-react/dist/commonjs/modules/Transition/TransitionGroup';
import Item from "./components/Item";
import Sidebar from "./components/Sidebar";
import EmailComposer from "./components/EmailComposer";

// const FETCHURL = "https://api.jsonbin.io/b/5a7cfa997ecc101273331408";
// const POSTURL = "https://api.jsonbin.io/b/5a84f744849a8f1c811922ad";
// const FETCHURL = "https://api.jsonbin.io/b/5a84f744849a8f1c811922ad/latest";
// const POSTURL = "https://api.jsonbin.io/b/5a9a0826a67185097469c99f";
// const FETCHURL = "https://api.jsonbin.io/b/5a9a0826a67185097469c99f/latest";
const POSTURL = "https://api.jsonbin.io/b/5a9e1a52859c4e1c4d5de983";
const FETCHURL = "https://api.jsonbin.io/b/5a9e1a52859c4e1c4d5de983/latest";

var newInput=true;

function titleCase(str) {
  console.log('titlecase',str.toLowerCase().split(' ').length);
  return str.toLowerCase().split(' ').map(function(word) {
    return (word.length > 0) ? word.replace(word[0], word[0].toUpperCase()) : "";
  }).join(' ');
}

const strCompare = (s1,s2) => {
  const u1 = s1.toUpperCase();
  const u2 = s2.toUpperCase();
  if (u1 < u2)
    return -1;
  if (u1 > u2)
    return 1;
  return 0;
}

const byName = (i1, i2) => {
  return strCompare(i1.name, i2.name);
}

const byTag = (i1, i2) => {
  let compare = strCompare(i1.tags, i2.tags)
  if (compare === 0)
    compare = strCompare(i1.name, i2.name)
  return compare;
}

const timeCompare = (t1, t2) => {
  let d1 = t1?t1:0
  let d2 = t2?t2:0
  if (d1 > d2)
    return 1
  if (d1 < d2)
    return -1
  return 0;
}

const byTimestamp = (i1, i2) => {
  return timeCompare(i1.timestamp, i2.timestamp);
}

const byDateDescThenByTimeAsc = (i1, i2) => {
  function compare(a, b) {
      return a > b || -(a < b);
  }
  var aISO = new Date(i1.timestamp).toISOString(),
      bISO = new Date(i2.timestamp).toISOString();
  return compare(bISO.slice(0, 10), aISO.slice(0, 10))   // date desc
      || compare(aISO.slice(11, 19), bISO.slice(11, 19)); // time asc
};


const byTagThenByTime = (i1, i2) => { 
  let compare = strCompare(i1.tags, i2.tags)
  if (compare === 0)
    compare = timeCompare(i1.timestamp, i2.timestamp)
  return compare;
}

const EMPTYITEM = {name:"", qty:0, unit:"", tags:"", notes:""};

class App extends Component {

  state = {
    filter:{name:"search-text", value:""}, //letter, search-text, category, none 
    searchText: "",
    items: [EMPTYITEM],
    openKeypad: false,
    marked:false,
    selectedItem:EMPTYITEM,
    saved:false,
    changed:false,
    status:"",
    sortMode:"Timestamp",
    reverse:false,
    openEmailComposer:false,
    selectedCategory: "",
    showCategoryModal:false,
    sidebarView:Sidebar.View.letters
  }


  // Fetch and save **********************

  componentDidMount() {
    fetch(FETCHURL)
        .then(response => response.json())
        .then(data => {
          // console.log('fetched');
          this.fetchedItems = data.Items;
          // for (let i=0; i<data.Items.length; i++)
          //   delete data.Items[i].timestamp; 
          var maxDate; // midnight 1970
          const todaysDate = (new Date()).setHours(0,0,0,0);
          const mappedItems = data.Items
            .sort(byDateDescThenByTimeAsc)
            .map((item, index) => {
              const _item = Object.assign({}, item);
              // console.log(index, _item);
              // console.log(index, _item.name, _item.timestamp);
              let itemDate = (new Date(_item.timestamp)).setHours(0,0,0,0);
              // console.log("itemDate", (new Date(itemDate)));
              // console.log("todaysDate", (new Date(todaysDate)));
              if (itemDate < todaysDate) {
               _item.qty="";
               _item.unit="";
               _item.timestamp=0;
                if (maxDate === undefined) {
                 maxDate=itemDate;
                }
                if (itemDate >= maxDate && item.qty.length > 0) {
                  _item.timestamp=(new Date()).setHours(0,0,index,0);
                }
              }
              // console.log(_item);
              return _item; 
            });
            // console.log(mappedItems);
          this.setState({items:mappedItems, lastStocktakeDate:maxDate, openKeypad:true})
        });
  }

  fetchedItems = {};

  changed = () => JSON.stringify(this.fetchedItems) === JSON.stringify(this.state.items);

  handleSendButtonClick = () => {
    this.setState({openEmailComposer:true})
  }

  handleSaveButtonClick = () => {
    this.sendData();
  }

  closeEmailComposer = () => {
    this.setState({openEmailComposer:false})
  }

  sendData = () => {
    this.setState({status:"saving"})
    this.postData(POSTURL, {Date: (new Date()).toJSON(), Items: this.state.items})
    .then(data => {
//      console.log(data);
      if (!data.success)
        throw new Error("Save failed");
      this.fetchedItems = data.data.Items;
//      console.log(this.changed());
      this.setState({saved:true, changed:false, status:"saved"})
      setTimeout(() => {
        this.setState({ saved: false, status:"" })
      }, 500)
    }) // JSON from `response.json()` call
    .catch(error => {
      this.setState({ saved: false, status:"error" })
      console.error(error)
    })
  }

  postData(url, data) {
    return fetch(url, {
      body: JSON.stringify(data), // must match 'Content-Type' header
      headers: {
        'content-type': 'application/json'
      },
      method: 'PUT',
    })
    .then(response => response.json()) // parses response to JSON
  }

// Item ******************************

  deleteItem = (item, e) => {
    const items = Object.assign([], this.state.items);
    const index = items.indexOf(item);
    items.splice(index,1);
    this.setState({items:items});
  }

  addItem = (category) => {
    console.log("addItem: ", category);
//    console.log(this.state.searchText);
    console.log(this.state.newItemName);
    const newItemName = titleCase(this.state.searchText);
    if (category) {
      const items = Object.assign([], this.state.items);
      const item = { name:newItemName, qty:"", unit:"", tags:category };
      items.push(item);
      this.setState({items:items, searchText:"", selectedItem:item, sidebarView:Sidebar.View.keypad});
    }
  }

  changeItemName = (e) => {
    console.log(e.target.value);
    const items = Object.assign([], this.state.items);
    const index = this.findItemIndexByName(this.state.selectedItem.name);
    const _item = items[index];
    _item.name = e.target.value;
    items[index] = Object.assign({},_item);
    this.setState({items:items, selectedItem:_item});
  }

  cancelAdd = () => {
    console.log("cancelAdd");
    this.setState({showCategoryModal:false})
  }

  handleAddClick = () => {
    this.setState({sidebarView:Sidebar.View.categories});
  }

  handleClick = (item,index,e) => {
//    console.log("handleClick: " + item.name);
    if (item.name === this.state.selectedItem.name) {
      console.log("same item");
      this.clearFilter();
      this.setState({selectedItem:EMPTYITEM, sidebarView:Sidebar.View.letters, searchText:""});//, () => setTimeout(() => this.ref.focus(),0));
      return;
    }
//      Without setTimeout the modal has not rendered when focus() is called.
//      setTimeouts() are executed last in the function all stack so 0 delay works!
    this.setState({selectedItem:item, sidebarView:Sidebar.View.keypad});//, () => setTimeout(() => this.ref.focus(),0));
    // this.openKeypad();
    newInput=true;
  }

  selectItem = (item,e) => {
//    console.log("selectItem: " + e.value);
    const index = this.findItemIndexByName(e.value);
//    console.log(index);
    const _item = this.state.items[index];
    this.setState({selectedItem:_item});
  }
 
// Filter ************************************

  setFilter = (name, value) => {
    console.log('setFilter', name, value);
    if (this.state.filter.name === name) 
      this.setState({filter:{name:"search-text", value:""}})
    else
      this.setState({filter:{name:name, value:value}})
  }

  clearFilter = () => {
    this.setFilter("search-text", "");
  }

  isFilteredBy = (name) => {
    return (this.state.filter.name === name && this.filterValue().length > 0)
  }

  isFiltered = () => {
    return (this.filterValue().length > 0 || this.state.searchText.length > 0);
  }

  filterValue = () => {
    return (this.state.filter.value);
  }

// Search ************************************

  changeSearchText = (e) => {
//    console.log(e.target.value);
      if (e.target.value.length > 0) {
        this.setState({searchText:e.target.value})
        //this.setFilter("search-text", e.target.value);
        // this.setSortMode("None");
      }
      else {
        this.clearSearchText(e);
      }
  }

  clearSearchText = () => {
    // console.log('clearSearchText:',e.target.value);
    // e.target.value = "";
//    this.searchRef.value = "";
//    this.clearFilter();
    this.setState({searchText: ""});
  }
  

  submitSearch = (addable,e) => {
//    console.log(addable);
    if (e.key === "Enter") {
      console.log("enter pressed");
      if (addable)
        this.handleAddClick();
    }
  }

  focusSearch = () => {
      this.searchRef.focus();
  }


  sendClicked = (e) => {
    this.setState({openEmailComposer: !this.state.openEmailComposer});//, () => setTimeout(() => this.ref.focus(),0));
  }

// Keypad *********************

  openKeypad = () => {
    this.setState({openKeypad: true});
  }

  closeKeypad = () => {
    if (this.state.openKeypad) {
      this.setState({openKeypad: false});
    }
  }


// Qty + Unit **************************

  toggleMarked = (e,p) => {
    if (!this.itemSelected())
      return;    
//    console.log(e.children);
    const items = Object.assign([], this.state.items);
    const index = this.findItemIndexByName(this.state.selectedItem.name);
    const _item = items[index];
    _item.marked = !_item.marked;
    items[index] = Object.assign({},_item);
    this.setState({items:items, selectedItem:_item});
  }

  itemSelected = () => (this.state.selectedItem.name.length > 0);

  setQty = (e,p) => {
    if (!this.itemSelected())
      return;    
    const items = Object.assign([], this.state.items);
    const index = this.findItemIndexByName(this.state.selectedItem.name);
    const _item = items[index];
    if (newInput || _item.qty.length > 1 || _item.qty==="0") {
      _item.qty="";
    }
    _item.qty = _item.qty + p.children;
    _item.timestamp = Date.now();
    items[index] = Object.assign({},_item);
    this.setState({items:items, selectedItem:_item, changed:true});
    // this.searchRef.value="";
    newInput=false;
  }

  clearQty = (e) => {
    if (!this.itemSelected())
      return;    
    const items = Object.assign([], this.state.items);
    const index = this.findItemIndexByName(this.state.selectedItem.name);
    const _item = items[index];
    _item.qty = "";
    _item.unit = "";
    _item.marked = "";
    // delete _item.timestamp;
    _item.timestamp = Date.now();
    items[index] = Object.assign({},_item);
    this.setState({items:items, selectedItem:_item});
  }

  setUnit = (e,p) => {
    if (!this.itemSelected())
      return;    
    const newUnit = p.children;
    const items = Object.assign([], this.state.items);
    const index = this.findItemIndexByName(this.state.selectedItem.name);
    const _item = items[index];
    if (_item.unit === newUnit)
      _item.unit = "";
    else
      _item.unit = newUnit;
    _item.timestamp = Date.now();
    items[index] = Object.assign({},_item);
    this.setState({items:items, selectedItem:_item});
  }

  setNote = (p,notes) => {
//    console.log("setNote", item, notes);
    if (!this.itemSelected())
      return;    
    const items = Object.assign([], this.state.items);
    const index = this.findItemIndexByName(this.state.selectedItem.name);
    const _item = items[index];
    _item.notes = notes;
//    _item.timestamp = Date.now();
    items[index] = Object.assign({},_item);
    this.setState({items:items, selectedItem:_item});
  }

  findItemIndexByName = (itemName) => {
    for (let i = 0; i < this.state.items.length; i++) {
      if (this.state.items[i].name === itemName)
        return i;
    }
    return -1;
  }


  statusToColor = () => {
    let ret = "black";
    switch (this.state.status) {
      case "saving" :
        ret = "black";
        break;
      case "saved" :
        ret = "green";
        break;
      case "error" :
        ret = "red";
        break;
      default :
        ret = "black";
    }
    return ret;
  }

// Sort ************************

  setSortMode = (sortMode) => {
    console.log("setSortMode", sortMode);
    this.setState({sortMode:sortMode});
  }

  reverse = () => {
    this.setState({reverse:!this.state.reverse});
  }


// AZ *****************************************  


selectLetter = (p,e) => {
  console.log(p.length);
    if (p ==="CLEAR")
      this.setState({searchText:""});
    else if (p==="⌫")
      this.setState({searchText: this.state.searchText.slice(0,-1)});
    else
      this.setState({searchText:(this.state.searchText + p).toLowerCase()});    
    // this.setState({searchText:titleCase(this.state.searchText + p)});    
  // e.stopPropagation();
      // if (this.isFilteredBy("letter") && this.filterValue() === p) {
      //   this.clearFilter();
      // }
      // else {
      //   this.setFilter("letter", p);
      // }
    }
    

  // selectLetter = (p,e) => {
  //   e.stopPropagation();
  //   if (this.isFilteredBy("letter") && this.filterValue() === p) {
  //     this.clearFilter();
  //   }
  //   else {
  //     this.setFilter("letter", p);
  //   }
  // }

  selectCategory = (category) => {
    console.log("selectCategory",category);
    if (this.isFilteredBy("category") && this.filterValue() === category) {
      this.clearFilter();
    }
    else {
      this.setFilter("category", category);
    }
  }

  handleNotesSubmit = (item,e) => {
      const items = Object.assign([], this.state.items);
      const index = this.findItemIndexByName(item.name);
      const _item = items[index];
      _item.notes = e.target.value;
      _item.marked = (_item.notes.length > 0);
      items[index] = Object.assign({},_item);
      this.setState({items:items});
  }

  handleSidebarViewChange = (viewName) => {
    console.log('handleSidebarViewChange', viewName)
    this.setState({sidebarView:Sidebar.View.letters});
    this.clearFilter();
  }

  componentDidUpdate() {
//    console.log("App.componentDidUpdate");
    if (this.isFilteredBy('completed'))
      window.scrollTo(0,0);
    return true;
  }

  handleCompletedClick = () => {
      if (this.isFilteredBy('completed'))
        this.clearFilter();
      else {
        // this.state.items.sort(byDateDescThenByTimeAsc);
        this.setFilter('completed', '*');
      }
  }

  handleDoneClick = () => {
    this.clearFilter();
    this.setState({searchText:""});
    this.handleSidebarViewChange("letters")
  }


  // Render ************************
  render() {
    var userSortable = false;
    var filteredItems;
    if (this.isFiltered()) {
      filteredItems = Object.assign([], this.state.items);
      if (this.isFilteredBy("completed")) {
        userSortable=true;
        filteredItems = filteredItems.filter((item, index) => {
          return (item.qty.length > 0 && (new Date(item.timestamp)).setHours(0,0,0,0) === (new Date().setHours(0,0,0,0)))
        });
      }
      else if (this.isFilteredBy("Fruit")) {
        userSortable=true;
        filteredItems = filteredItems.filter((item, index) => {
          return (item.type === this.state.filter.name);//  && (new Date(item.timestamp)).setHours(0,0,0,0) === (new Date().setHours(0,0,0,0)))
        });
      }
      else if (this.isFilteredBy("Veg")) {
        userSortable=true;
        filteredItems = filteredItems.filter((item, index) => {
          return (item.type === this.state.filter.name);//  && (new Date(item.timestamp)).setHours(0,0,0,0) === (new Date().setHours(0,0,0,0)))
        });
      }
      else if (this.isFilteredBy("All Fruit")) {
        // userSortable=true;
        filteredItems = filteredItems.filter((item, index) => {
          return (item.type === 'Fruit')
        });
      }
      else if (this.isFilteredBy("All Veg")) {
        // userSortable=true;
        filteredItems = filteredItems.filter((item, index) => {
          return (item.type === 'Veg')
        });
      }    
      else if (this.isFilteredBy("category")) {
        filteredItems = filteredItems.filter((item, index) => {
          return (item.tags === this.state.filter.value)
        });
      }
      else if (this.isFilteredBy("letter")) {
        filteredItems = filteredItems.filter((item, index) => {
          return (item.name.charAt(0).toUpperCase() === this.state.filter.value.charAt(0).toUpperCase())
        });
      }
      if (this.state.searchText.length === 1) {
        filteredItems = filteredItems.filter((item, index) => {
          return (item.name.charAt(0).toUpperCase() === this.state.searchText.charAt(0).toUpperCase())
        });
      }
      else if (this.state.searchText.length > 1) {
        filteredItems = filteredItems.filter((item, index) => {
          return (
            item.name.toUpperCase().includes(this.state.searchText.toUpperCase())
            || item.tags.toUpperCase().includes(this.state.searchText.toUpperCase())
          )
        });
      }
    }
    else {
      userSortable=true;
      const todaysDate = (new Date()).setHours(0,0,0,0);
      filteredItems = this.state.items.filter((item, index) => {
        // console.log(item.timestamp, todaysDate);
        return (item.qty.length > 0 && item.timestamp >= todaysDate); 
      });
    }

    if (userSortable) {
      const sortMode = this.state.sortMode;
      if (sortMode === 'Alphabetical')
        filteredItems.sort(byName);
      if (sortMode === 'Grouped')
        filteredItems.sort(byTag);
      // if (sortMode === 'Timestamp') 
      //     filteredItems.sort(byDateDescThenByTimeAsc);
      if (this.state.reverse)
        filteredItems.reverse();
    }
    else {
      console.log('sorting by name');
      filteredItems.sort(byName)
    }
    
    const items = filteredItems.map((item,index) => {
      return (
        <Item
          key={item.name}
          item={item}
          selectedItem={this.state.selectedItem}
          index={index}
          selected={(item.name === this.state.selectedItem.name)}
          deleteEvent={this.deleteItem.bind(this, item)}
          rowClickEvent={this.handleClick.bind(this, item, index)}
          onNotesSubmit={this.handleNotesSubmit.bind(this, item)}
        > {item.name}</Item>)
    })
    const addable = items.length === 0 && this.state.searchText.length > 3;
    const adding = this.isFilteredBy("category") && this.state.searchText.length > 3;
    const addSelectCat = this.state.sidebarView === Sidebar.View.categories && !this.isFilteredBy("category");
    let sidebarView = Sidebar.View.letters;
    if (this.state.showCategoryModal)
      sidebarView = Sidebar.View.categories;
    
    return (
      <div>
        <Sidebar
          items={this.state.items}
          item={this.state.selectedItem}
          // open={this.state.openKeypad || addable || adding}
          view={this.state.sidebarView}
          viewChangeHandler={this.handleSidebarViewChange} 
          // adding={addable || adding}
          letterSelected={this.selectLetter}  
          categorySelected={this.selectCategory}
          sortMode={this.state.sortMode}
          sortModeSelected={this.setSortMode}
          sortDirectionChanged={this.reverse}
          reverse={this.state.reverse}
          numberClicked={this.setQty}
          unitClicked={this.setUnit}
          deleteClicked={this.clearQty}
          asteriskClicked={this.toggleMarked}
          showSimilar={this.selectCategory}
          completedClicked={this.handleCompletedClick}
          fruitSelected={this.setFilter}
          vegSelected={this.setFilter}
          noteEntered={this.setNote}
          filter={this.state.filter}
          clearSearchText={this.clearSearchText}
          doneClicked={this.handleDoneClick}
        />
          <Label basic size="big" pointing="right"
              style={{
                visibility:(addSelectCat)?'visible':'hidden',
                position:'fixed', top:'25%', right:'50%',
                boxShadow: "0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22)"
              }}
              content={"Select a category to add " + titleCase(this.state.searchText)}
          />
          {/* <Label size="big" pointing="above"
              style={{
                visibility:(adding)?'visible':'hidden',
                position:'fixed', bottom:'25%', right:'50%',
                boxShadow: "0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22)"
              }}
          >
            Add here
            <p/>
            <Button.Group compact>
              <Button negative>Cancel</Button>
              <Button positive>Add</Button>
            </Button.Group>
          </Label> */}
        
        { addable ?
          <Button secondary size="massive" icon="add"
            // disabled={addSelectCat}
            style={{
              position:'fixed', left:'15%', top:'50%', zIndex:100000,
              boxShadow: "0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22)"
            }}
            onClick={this.handleAddClick}
          /> :
          null
        }
        { adding ? 
          <Button secondary size="massive" icon="add"
            style={{
              position:'fixed', left:'15%', top:'50%', zIndex:100000,
              boxShadow: "0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22)"
            }}
            onClick={this.addItem.bind(this, this.filterValue())}
          /> :
          null
        }
        <EmailComposer 
          items={this.state.items
            .filter((item,index) => (item.qty.length > 0 && 
              (new Date(item.timestamp)).setHours(0,0,0,0) === (new Date()).setHours(0,0,0,0)))
            .sort(byTagThenByTime)}
          open={this.state.openEmailComposer}
          onClose={this.closeEmailComposer}
        />
        <Menu size="massive" widths={16} className="ui fixed compact inverted" style={{height:62}}>
          <Menu.Item>
            <Header inverted>{this.state.searchText}</Header> 
          </Menu.Item>
            <Menu.Item position="right"  style={{width:72, position:'fixed', top:0, right:0}}>
              { this.state.searchText.length > 0 ?
                <Button className="black" size="huge" icon="cancel"
                  onClick={this.clearSearchText}
                />
                : null
              }
              { (this.state.searchText.length === 0  && this.state.changed) ? 
                <Button color={this.statusToColor()} size="huge" icon="save"
                  onClick={this.handleSaveButtonClick}
                />
              : null
              }
              { (this.state.searchText.length === 0  && !this.state.changed) ? 
                <Button color={this.statusToColor()} size="huge" icon="save"
                  onClick={this.handleSendButtonClick}
                />
              : null
              }
            </Menu.Item>
          {/* <Menu.Item position="left"  style={{width:72, position:'fixed', top:0, left:0}}>
            <Button color={this.statusToColor()} size="huge" icon={this.state.changed?"save":"send"}
              onClick={this.handleSaveSendButtonClick}
            />
          </Menu.Item> */}
        </Menu>
        {/* <Button size="huge" secondary  
          icon={addable?"add":"search"} 
          style={{position: 'fixed', top:6, left:6, display:'block', zIndex:700  }} onClick={this.focusSearch}
        /> */}
        <Table inverted unstackable selectable={false} striped={false} fixed width={16} style={{width:'50%', marginTop:62, marginBottom:'100%'}}>
          <Table.Body>
            {items}
          </Table.Body>
        </Table>
      </div>
    );
  }
}

export default App;



