import React, { Component } from 'react';
import {TransitionablePortal, Segment, Button} from 'semantic-ui-react';

class IndexPortal extends Component {

    state = { selectedLetter: "", startingLetters:[] }
    
    render() {  
        return (
            <div>
                <TransitionablePortal
                    open={this.props.open}
                    transition={{animation:'horizontal flip', duration:500}}
                    closeOnDocumentClick={false}
                    // onClose={this.props.onClose}
                    onOpen={this.onOpenAZ}
                >  
                    <Segment basic inverted floated="right" textAlign="center" 
                        style={{
                            marginRight:0,
                            paddingTop:0,
                            borderRadius:"0em",
                            position:'fixed',
                            top: '62px',
                            right: '0%',
                            zIndex: 5002,
                            height:"auto",
                            width:"50%"
                        }}  
                        onClick={this.stopPropagation}
                    >
                        <Button.Group widths="3" compact className="black" style={{height:40}} >
                            <Button onClick={this.props.letterSelected.bind(this, 'A')} style={{borderRadius:"0em"}}>A</Button>
                            <Button onClick={this.props.letterSelected.bind(this, 'B')} style={{borderRadius:"0em"}}>B</Button>
                            <Button onClick={this.props.letterSelected.bind(this, 'C')} style={{borderRadius:"0em"}}>C</Button>
                        </Button.Group>
                        <Button.Group widths="3" compact className="black" style={{height:40}} >
                            <Button onClick={this.props.letterSelected.bind(this, 'D')} style={{borderRadius:"0em"}}>D</Button>
                            <Button onClick={this.props.letterSelected.bind(this, 'E')} style={{borderRadius:"0em"}}>E</Button>
                            <Button onClick={this.props.letterSelected.bind(this, 'F')} style={{borderRadius:"0em"}}>F</Button>
                        </Button.Group>
                        <Button.Group widths="3" compact className="black" style={{height:40}} >
                            <Button onClick={this.props.letterSelected.bind(this, 'G')} style={{borderRadius:"0em"}}>G</Button>
                            <Button onClick={this.props.letterSelected.bind(this, 'H')} style={{borderRadius:"0em"}}>H</Button>
                            <Button onClick={this.props.letterSelected.bind(this, 'I')} style={{borderRadius:"0em"}}>I</Button>
                        </Button.Group>
                        <Button.Group widths="3" compact className="black" style={{height:40}} >
                            <Button onClick={this.props.letterSelected.bind(this, 'J')} style={{borderRadius:"0em"}}>J</Button>
                            <Button onClick={this.props.letterSelected.bind(this, 'K')} style={{borderRadius:"0em"}}>K</Button>
                            <Button onClick={this.props.letterSelected.bind(this, 'L')} style={{borderRadius:"0em"}}>L</Button>
                        </Button.Group>
                        <Button.Group widths="3" compact className="black" style={{height:40}} >
                            <Button onClick={this.props.letterSelected.bind(this, 'M')} style={{borderRadius:"0em"}}>M</Button>
                            <Button onClick={this.props.letterSelected.bind(this, 'N')} style={{borderRadius:"0em"}}>N</Button>
                            <Button onClick={this.props.letterSelected.bind(this, 'O')} style={{borderRadius:"0em"}}>O</Button>
                        </Button.Group>
                        <Button.Group widths="3" compact className="black" style={{height:40}} >
                            <Button onClick={this.props.letterSelected.bind(this, 'P')} style={{borderRadius:"0em"}}>P</Button>
                            <Button onClick={this.props.letterSelected.bind(this, 'Q')} style={{borderRadius:"0em"}}>Q</Button>
                            <Button onClick={this.props.letterSelected.bind(this, 'R')} style={{borderRadius:"0em"}}>R</Button>
                        </Button.Group>
                        <Button.Group widths="3" compact className="black" style={{height:40}} >
                            <Button onClick={this.props.letterSelected.bind(this, 'S')} style={{borderRadius:"0em"}}>S</Button>
                            <Button onClick={this.props.letterSelected.bind(this, 'T')} style={{borderRadius:"0em"}}>T</Button>
                            <Button onClick={this.props.letterSelected.bind(this, 'U')} style={{borderRadius:"0em"}}>U</Button>
                        </Button.Group>
                        <Button.Group widths="3" compact className="black" style={{height:40}} >
                            <Button onClick={this.props.letterSelected.bind(this, 'V')} style={{borderRadius:"0em"}}>V</Button>
                            <Button onClick={this.props.letterSelected.bind(this, 'W')} style={{borderRadius:"0em"}}>W</Button>
                            <Button onClick={this.props.letterSelected.bind(this, 'X')} style={{borderRadius:"0em"}}>Z</Button>
                        </Button.Group>
                        <Button.Group widths="3" compact className="black" style={{height:40}} >
                            <Button onClick={this.props.letterSelected.bind(this, 'Y')} style={{borderRadius:"0em"}}>Y</Button>
                            <Button onClick={this.props.letterSelected.bind(this, 'Z')} style={{borderRadius:"0em"}}>Z</Button>
                            <Button onClick={this.props.letterSelected.bind(this, '⌫')} style={{borderRadius:"0em"}}>⌫</Button>
                        </Button.Group>
                        <Button className="black"  onClick={this.props.letterSelected.bind(this, " ")} style={{borderRadius:0}}>[SPACE]</Button>
                        {/* <Button icon="delete" size="huge" inverted onClick={this.props.clearSearchText} style={{borderRadius:0, margin:20, opacity:0.7}}/> */}
                        <p/>
                        <p/>
                        {/* <Button.Group size="massive" compact className="black">
                            <Button icon="apple"
                                style={{opacity:(this.props.filter.name === 'Fruit')?1:0.5}}   
                                onClick={this.props.fruitSelected.bind(this, 'Fruit',"*")}
                            />
                            <Button icon="leaf"
                                style={{opacity:(this.props.filter.name === 'Veg')?1:0.5}}   
                                onClick={this.props.vegSelected.bind(this, 'Veg',"*")}
                            />
                        </Button.Group>                     */}
                    </Segment>
                </TransitionablePortal>
            </div>
        );
    }
}

export default IndexPortal;



